.btn--animation:hover, .btn--animation:focus {
  animation: turning .75s linear .25s alternate forwards;
}

.btn__list {
  height: 20px;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
  overflow: hidden;
}

.btn--animation:hover, .btn--animation:focus {
  -webkit-box-reflect: below 5px linear-gradient(transparent, #fff);
  background-color: #b5ff00;
  box-shadow: 0 0 5px #b5ff00, 0 0 25px #b5ff00, 0 0 50px #b5ff00, 0 0 100px #b5ff00;
}

.btn__item {
  border-radius: 30px;
  display: block;
  position: absolute;
}

.btn__item:nth-child(1) {
  width: 100%;
  height: 5px;
  background: linear-gradient(90deg, rgba(0, 0, 0, 0), #ff0);
  animation: btn-anim1 2s linear infinite;
  top: 0;
  left: -100%;
}

.btn__item:nth-child(2) {
  width: 5px;
  height: 100%;
  background: linear-gradient(rgba(0, 0, 0, 0), #ff0);
  animation: btn-anim2 2s linear .5s infinite;
  top: -100%;
  right: 0;
}

.btn__item:nth-child(3) {
  width: 100%;
  height: 5px;
  background: linear-gradient(270deg, rgba(0, 0, 0, 0), #ff0);
  animation: btn-anim3 2s linear 1s infinite;
  bottom: 0;
  right: -100%;
}

.btn__item:nth-child(4) {
  width: 5px;
  height: 100%;
  background: linear-gradient(360deg, rgba(0, 0, 0, 0), #ff0);
  animation: btn-anim4 2s linear 1.5s infinite;
  bottom: -100%;
  left: 0;
}

.btn--animation:focus .btn__item, .btn--animation:hover .btn__item {
  animation: none;
}

@keyframes turning {
  0% {
    border-radius: 30px;
    transform: rotate(0deg);
  }

  25% {
    border-radius: 30px 30px 30px 0;
    transform: rotate(90deg);
  }

  50% {
    border-radius: 30px 30px 0 0;
    transform: rotate(180deg);
  }

  75% {
    border-radius: 30px 0 0;
    transform: rotate(270deg);
  }

  100% {
    border-radius: 0;
    transform: rotate(360deg);
  }
}

@keyframes heartBeat {
  0% {
    transform: translate(-50%, -50%);
  }

  20% {
    transform: translate(-50%, -50%)scale(1.15);
  }

  40% {
    transform: translate(-50%, -50%)scale(.9);
  }

  60% {
    transform: translate(-50%, -50%)scale(1.1);
  }

  80% {
    transform: translate(-50%, -50%)scale(1);
  }

  100% {
    transform: translate(-50%, -50%)scale(1);
  }
}

@keyframes btn-anim1 {
  0% {
    left: -100%;
  }

  50%, 100% {
    left: 100%;
  }
}

@keyframes btn-anim2 {
  0% {
    top: -100%;
  }

  50%, 100% {
    top: 100%;
  }
}

@keyframes btn-anim3 {
  0% {
    right: -100%;
  }

  50%, 100% {
    right: 100%;
  }
}

@keyframes btn-anim4 {
  0% {
    bottom: -100%;
  }

  50%, 100% {
    bottom: 100%;
  }
}

.backdrop {
  z-index: 1000;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .2);
  transition: opacity .4s ease-in-out, visibility .4s ease-in-out;
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.modal {
  z-index: 30;
  width: 320px;
  width: 100%;
  height: 449px;
  background-image: radial-gradient(circle, #fff, #ffe550);
  border-radius: 30px;
  padding: 32px 20px;
  animation: heartBeat 2s ease-in-out .2s both;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
  box-shadow: 0 4px 24px rgba(105, 105, 105, .1);
}

@media screen and (min-width: 480px) {
  .modal {
    max-width: 458px;
    margin: 0 auto;
    padding: 32px;
  }
}

.backdrop.is-hidden .modal {
  animation-play-state: paused;
  transform: translate(0, -100%)scale(1.1);
}

