//Hero styles


.hero {
  // display: inline-flex;
  // flex-direction: column;
  // justify-content: center;

  // width: 100%;

  // @include media-query(tablet) {
  //   max-width: 537px;
  // }

  // @include media-query(desktop) {
  //   max-width: 502px;
  // }

        @include media-query(desktop) {
          padding: 14px 0;
        }



  &__main {
      @include media-query(tablet) {
        position: relative;
  
        // height: 409px;
      }
  
      @include media-query(desktop) {
        display: flex;
        justify-content: space-between;
  
        // text-align: left;
  
        // height: 608px;
      }
    }

  &__block {
      display: inline-flex;
      flex-direction: column;
      justify-content: space-between;
  }


  &__title {
    margin-bottom: 28px;
    font-weight: 700;
    color: $color-black-second;
    font-size: 28px;
    line-height: calc(33 / 28);

    // display: block;
    max-width: 280px;

    @include media-query(tablet) {
      max-width: 537px;
      font-size: 42px;
      line-height: calc(50 / 42);
      margin-bottom: 44px;
    }

    @include media-query(desktop) {
      width: 502px;
      font-size: 48px;
      line-height: calc(58 / 48);
    }
  }

  &__text {
    // display: block;
    margin-bottom: 28px;

    color: $color-black-second;
    font-size: 14px;
    line-height: calc(21 / 14);

    @include media-query(tablet) {
      font-size: 16px;
      line-height: calc(24 / 16);
      width: 336px;
      margin-bottom: 40px;
    }

    @include media-query(desktop) {
      font-size: 16px;
      line-height: calc(24 / 16);
      width: 502px;
    }
  }

  &__btn {
    display: block;
    font-family: inherit;
    color: $color-black-second;
    font-weight: 500;
    font-size: 13px;
    line-height: calc(20 / 13);

    align-items: center;

    cursor: pointer;

    background-color: $color-green;
    border: none;
    border-radius: 30px;
    padding: 6px 18px;
    width: 98px;
  }
  &__btn:hover,
  &__btn:focus,
  &__btn:active {
    background-color: $color-green-accent;
  }
}

.slider {
  display: none;

  @include media-query(tablet) {
    position: relative;
    bottom: 0;
    right: 32px;
    display: flex;
    width: 336px;
    height: 320px;
  }
  @include media-query(desktop) {
    // top: 14px;
    // right: 16px;
    width: 608px;
    height: 580px;
    border:0;
  }
}
.swiper {
  display: none;

  @include media-query(tablet) {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 320px;
    height: 320px;
  }
  @include media-query(desktop) {
    width: 608px;
    height: 608px;
  }
}
.swiper-slide {
  display: none;
  @include media-query(tablet) {
    display: flex;

    border: 14px solid $color-yellow;
    border-radius: 50%;
  }

  @include media-query(desktop) {
    border: 26px solid $color-yellow;
  }
}

.swiper-button-next {
  right: 0px;
}
.swiper-button-prev {
  left: 0px;
}

.swiper-button-next,
.swiper-button-prev {
  color: $color-black-second;
  background-color: $color-white;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.06);
  border-radius: 4px;
  cursor: pointer;

  @include media-query(tablet) {
    width: 34px;
    height: 28px;
  }
  @include media-query(desktop) {
    width: 48px;
    height: 40px;
  }

  &:after,
  &:after {
    @include media-query(tablet) {
      font-size: 20px;
      text-align: center;
    }
    @include media-query(desktop) {
      font-size: 28px;
      text-align: center;
    }
  }
  &:hover,
  &:hover,
  &:focus,
  &:focus {
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.16);
    color: $color-green-dark;
  }
  &:active,
  &:active {
    color: $color-black-second;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.06);
  }
}
